.searchtable {
    width: 100%;
    height: 300px;
    margin-top: -1px;
    margin-left: -1px;
    border-radius: 4px;
    overflow: auto;

    border: 1px solid #ccc;

    ul {
        list-style: none;
        padding: 10px;

        li {
            margin-bottom: 10px;

            .userbox {
                float: left;
                max-width: 150px;
                font-size: 20px;
                margin-top: -10px;
                margin-left: 10px;
                overflow-wrap: break-word;
            }

            .addButton {
                color: #636b76;
                cursor: pointer;
                margin-left: 7px;
                font-size: 25px;

                :hover {
                    color: green
                }
            }

            .deleteButton {
                color: #636b76;
                cursor: pointer;
                margin-left: 7px;
                font-size: 25px;

                :hover {
                    color: red
                }
            }
        }
    }
}


.logout {
    float: right;
    width: 80px;
    margin-top: 10px;
    line-height: 10px;
    font-size: 23px;
}

.username {
    text-align: center;
    height: 15px;
    font-size: 23px;
    color: #636b76;
    margin-top: -20px;
}

.chatbox {
    height: 470px;
    overflow-y: scroll;
    overflow-x: hidden;

    .chatbox-item {
        margin-top: 5px;

        .chatbox-item-avatar {
            img {
                width: 40px;
                height: 40px;
            }
        }

        .chatbox-item-content {
            .chatbox-item-content-name-and-time {
                position: relative;
                top: -48px;
                left: 47px;
                font-weight: 700;
                margin-left: 5px;
            }

            .chatbox-item-content-message {
                position: relative;
                top: -45px;
                left: 48px;
                max-width: 500px;
                min-height: 30px;
                border-radius: 8px;
                padding: 3px 8px 3px 8px;
                font-size: 18px;
                overflow-wrap: break-word;
                margin-bottom: 0;

                img {
                    max-width: 500px;
                }
            }

        }
    }
}


.sendbox {
    position: relative;
    width: 100%;
    top: -30px;

    em-emoji-picker {
        position: absolute;
        top: -220px;
        left: 10px;
        height: 200px;

        #preview {
            display: none;
        }

        element.style {
            display: none;
        }
    }

    .chat {
        img {
            max-height: 200px;
        }
    }
}